const state = {
    markets: [],
    contracts: [],
    info: undefined,
    pollSpeedup: 0
};

const mutations = {
    set_markets(state, markets) {
        state.markets = markets;
    },
    set_contracts(state, contracts) {
        state.contracts = contracts;
    },
    set_globalInfo(state, info) {
        state.info = info;
    },
    set_poolSpeed(state, n) {
        const speed = state.pollSpeedup + n;
        if (speed < 0) return;
        state.pollSpeedup = speed > 10 ? 10 : speed;
    },
};

const actions = {};

export default {
    state,
    mutations,
    actions
}
