import BigNumber from "bignumber.js";
import { DECIMAL_PLACES } from "../config/constants"
import { ethers } from "ethers";
// _ethers.utils.formatUnits("23234234", 2) // "232342.34"
// _ethers.utils.parseUnits("234.12",2)  // 23412
/**
 * 格式化地址
 * @param {string} address
 * @param {number} bef 前面显示几位
 * @param {number} aft 后面显示几位
 */
export function formatAddress(address, bef = 6, aft = 6) {
  try {
    return address.substr(0, bef) + "..." + address.substr(-aft);
  } catch (error) {
    return "--";
  }
}

/**
 * 保留小数
 * @param {*} number
 * @param {*} decimal
 */
export function toDecimal(number, decimal = 2) {
  const result = new BigNumber(number).decimalPlaces(decimal).toFormat();
  return result === "NaN" ? "--" : result;
}

export function formatUnits(numStr, decimal) {
  return ethers.utils.formatUnits(numStr, decimal)  // 23412
}

/**
 * 格式化金额
 * @param {*} number
 * @param {*} precision
 * @param {*} decimal
 */
export function fromToken(number, precision = 0, decimal = DECIMAL_PLACES) {
  if (number && number._isBigNumber) {
    number = number.toString();
  }
  if (precision && precision._isBigNumber) {
    precision = precision.toNumber();
  }
  // console.log('number---', number, 'precision', precision, 'decimal', decimal)
  try {
    const amount = new BigNumber(number)
      .shiftedBy(-precision)
      .decimalPlaces(decimal)
      .toFormat();

    if (amount === "NaN") return "--";
    return amount;
  } catch (error) {
    throw new Error(error);
  }
}

// 输入框数数量乘以精度
export function toToken(number, precision) {
  return number ? ethers.utils.parseUnits(number, precision).toString() : "0";
  /*
  const amount = new BigNumber(number).shiftedBy(precision).toString();
  if (amount === "NaN") return "--";
  return amount;*/
}

// to percent
export function toPercent(number, total) {
  if (number == 0 || total == 0) {
    return 0;
  }
  return (Math.round(number * total * 100) / 100.00);// 小数点后两位百分比
}

export function compare(n1, n2) {
  //console.log(n1.toString(), n2.toString())
  return ethers.BigNumber.from(n1 || 0).sub(n2 || 0)
}
