import Vue from 'vue';
import Vuex from 'vuex';
import getters from "./getters";
import layout from "./modules/layout";
import user from "./modules/user";
import dashboard from "./modules/dashboard";
import WBTC from "./modules/WBTC";
import WETH from "./modules/WETH";
import USDT from "./modules/USDT";
import { switchChain, connect, selectedAddress } from '../utils/ethereum';
import { getMarkets } from '../compound/compound';



Vue.use(Vuex)

export default new Vuex.Store({
  // state: {
  // },
  // mutations: {
  // },
  actions: {
    async pollTask({ commit, state }) {
      if (state.dashboard.contracts.length == 0) commit('set_contracts', getMarkets(window.ethereum.chainId));
      const contracts = state.dashboard.contracts;
      if(contracts.length == 0) return;
      let info = await contracts.clens.marketInfo(state.user.address);
      commit('set_globalInfo', info);
      commit('set_supplyBalance', info.supplyBalance.toString());
      commit('set_netAPY', info.netAPY.toString());
      commit('set_borrowBalance', info.borrowBalance.toString());
      {
        let borrowLimit = info.borrowLimit();
        commit('set_borrowed', borrowLimit[0]);
        commit('set_totalLimit', borrowLimit[1]);
        commit('set_borrowedLimitPercent', borrowLimit[2]);
      }
      commit('set_markets', info.markets);

      if (state.user.address)
        commit('set_userInit', true);
    },
    async CommitTx(_, tx) {
      await tx.wait(1);
      await this.dispatch('pollTask');
    },
    async switchNet(_, net) {
      await switchChain(net);
    },
    async login({ commit }, force = false) {
      if (selectedAddress() || force) {
        await connect({
          set_address: (address,first=false) => {
            commit('set_userInit', false);
            commit('set_address', address);
            if(!first) this.dispatch('pollTask');
          },
          set_chainID: (chainID,first=false) => {
            commit('set_userInit', false);
            commit('set_chainID', chainID);
            commit('set_contracts', getMarkets(chainID));
            if(!first) this.dispatch('pollTask');
          }
        })
      }
    }
  },
  modules: {
    layout,
    user,
    dashboard,
    WBTC,
    WETH,
    USDT
  },
  getters,
})
